.profile-summary, .profile-widget {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.profile-image {
  flex: 0 0 100px;
  margin-right: 10px;
  border-radius: 50%;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center center;
}
.profile-meta {
  flex: 1;
  h2 {
    margin: 0;
  }
  p {
    margin: 0;
  }
  .profile-socials {
    display: flex;
    flex-flow: column nowrap;
    width: min-content;
    background-color: $primary;
    border-radius: 3px;
    position: relative;
    padding: 0.1em 0.6em 0;

    .socials-callout {
      color: $white;
      font-family: "Nothing You Could Do", cursive;
      min-width: max-content;
      }
      
      .socials-buttons {
        display: flex;
        flex-flow: row nowrap;
    }

    a.icon {
      text-decoration: none;
      position: relative;
      transition: color ease-in-out 200ms;
      color: $white;
      
      &:hover {
        &.icon-teams {
          color: #4b53bc;
        }
        &.icon-email {
          color: #0078d4;
        }
        &.icon-people {
          color: #ed007e;
        }
        &.icon-linkedin {
          color: #0a66c2;
        }
        &.icon-website {
          color: #005bf6;
        }
      }
    }
  }
}
.profile-content {
  flex: 1 0 100%;
  margin-top: 1rem;
}
