$grid-gutter-width: 20px;

$white: #ffffff;
$black: #000;
$primary: #008eaa;
$secondary: #1f2229;

// Links
$link-color: var(--link-colour, var(--accent-colour, $primary));
$link-decoration: none;
$link-hover-color: var(--link-colour, var(--accent-colour, $primary));
$link-hover-decoration: underline;

// Fonts
$font-family-base: "Open Sans", "Helvetica Neue", Arial, sans-serif,
  -apple-system;
$font-family-heading: "Roboto Condensed", "Helvetica Neue", Arial, sans-serif,
  -apple-system;
$headings-font-family: $font-family-heading; // template got bootstrap variable name wrong?
$font-size-base: 1rem; // Assumes the browser default, typically `16px`
$blockquote-font-size: ($font-size-base * 1.1);
$font-weight-thin: 200;
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-bold: 700;
$font-weight-base: $font-weight-normal;
$line-height-base: 1.6;

$headings-font-weight: 400;

$spacer: 1rem;
$spacers: ();
$spacers: map-merge(
  (
    0: 0,
    1: 10px,
    2: 20px,
    3: 30px,
    4: 40px,
    5: 50px,
    6: 60px,
    7: 70px,
    8: 80px,
    9: 90px,
    10: 100px,
    11: 110px,
    12: 120px,
    13: 130px,
    14: 140px,
    15: 150px,
    16: 160px,
    17: 170px,
    18: 180px,
    19: 190px,
    20: 200px,
  ),
  $spacers
);

$container-max-widths: (
    sm: 540px,
    md: 720px,
    lg: 840px,
    xl: 920px,
);
